export default{
	//这是卷的下面
	data(){
		return {
			modalName: null,
		}
	},
	methods:{
		receive(e){
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			//隐藏窗口
			this.modalName = null;
			//加入到缓存里面
		},
	}
}
